




























import { Component, Vue } from "vue-property-decorator";
import { LoginForm, LoginResponseT } from "@/microApps/baseLogin/interface";
import { FormRules } from "@/components/elementUI/form";
import { ElForm } from "element-ui/types/form";
import cookieUtils from "@/utils/cookie";
import axios from "axios";

@Component({})
export default class App extends Vue {
  form: LoginForm = {
    username: "",
    password: ""
  };
  formRules: FormRules = {
    username: { required: true, message: "请输入用户名", trigger: "change" },
    password: { required: true, message: "请输入密码", trigger: "change" }
  };

  handleLogin(): void {
    (this.$refs.form as ElForm).validate(isValid => {
      if (isValid) {
        this.doLogin();
      }
    });
  }

  async doLogin() {
    const formData: FormData = new FormData();
    formData.append("username", this.form.username);
    formData.append("password", this.form.password);
    let {
      data: { status, data, message }
    } = await axios.post<ResponseEntity<LoginResponseT>>(`/api/user/login`, formData);
    if (status) {
      cookieUtils.set("accessToken", data.accessToken);
      localStorage.setItem("menus", JSON.stringify(data.menus));
      localStorage.setItem("realName", data.realName);
      localStorage.setItem("userName", data.username);
      localStorage.setItem("orgIds", JSON.stringify(data.orgIds));
      // 存储基本数据类型权限Map
      let baseTypeCodeMap: { [key: string]: boolean } = {};
      data.baseTypes.forEach(item => {
        baseTypeCodeMap[item.code] = true;
      });
      localStorage.setItem("baseTypeCodeMap", JSON.stringify(baseTypeCodeMap));
      qTouch.navigateToUrl(qTouch.appPathMap.root);
    } else {
      this.$message.error(message);
    }
  }
}
